import React from 'react';
import {
  HashRouter,
  Route,
  Switch,
  Redirect
} from "react-router-dom";

import './styles/styles.css';

import SidebarDesktop from "./ui/SidebarDesktop";
import SidebarMobile from "./ui/SidebarMobile";
import HomePage from "./routes/HomePage";
import TimeManagemet from "./routes/TimeManagemet";
import IndexCopmonent from "./routes/IndexCopmonent";
import Calendar from "./routes/Calendar";
import Analytics from "./routes/Analytics";
import Settings from "./routes/Settings";


const App = () => {
  return (
    <HashRouter>
      <div className="app">
        <div className="app__menu">
          <SidebarDesktop />

          <SidebarMobile />
        </div>

        <Switch>
          <Route path="/home">
            <HomePage />
          </Route>
          <Route path="/time">
            <TimeManagemet />
          </Route>
          <Route path="/index">
            <IndexCopmonent />
          </Route>
          <Route path="/calendar">
            <Calendar />
          </Route>
          <Route path="/analytics">
            <Analytics />
          </Route>
          <Route path="/settings">
            <Settings />
          </Route>
          <Route path="*">
            <Redirect to="/home" />
          </Route>
        </Switch>
      </div>
    </HashRouter>
  );
};

export default App;
