import React from 'react';

import { HorizontalBar, Line, Doughnut } from 'react-chartjs-2';
import detailsPie from "../detailsCharts/detailsPie";
import detailsBar from "../detailsCharts/detailsBar";
import detailsLine from "../detailsCharts/detailsLine";

const HomePage = () => {

  return (
    <div className="main">
      <div className="main__gray-line"></div>
      <div className="main__title-block">
        <h2 className="main__title-block--title">
          Hi Filip,
        </h2>
        <h4 className="main__title-block--subtitle">
          Checkout your latest projects and their progress.
        </h4>
      </div>

      <div className="charts">
        <div className="charts__block charts__line">
          <div className="charts__title-block">
            <div className="charts__title-block--wrapper">
              <h2 className="charts__title-block--title">
                Wavy Lines
            </h2>
              <h4 className="charts__title-block--subtitle">
                Working Hours
              </h4>
            </div>
            <div className="charts__title-block--wrapper">
              <p className="charts__title-block--option">This week</p>
            </div>
          </div>
          <Line
            options={{
              responsive: true,
              scales: {
                yAxes: [
                  {
                    ticks: {
                      fontColor: "grey",
                      fontSize: 16,
                      fontStyle: "normal",
                      beginAtZero: true,
                      min: 0,
                      padding: 15
                    }
                  }
                ],
                xAxes: [
                  {
                    gridLines: {
                      display: false
                    },
                    ticks: {
                      fontColor: "grey",
                      fontSize: 16,
                      fontStyle: "normal",
                      beginAtZero: true,
                      min: 0,
                      padding: 15
                    }
                  }
                ]
              },

            }}
            data={detailsLine}
            legend={{ display: false }}
          />
        </div>

          <div className="charts__block charts__pie">
            <Doughnut data={detailsPie} legend={{ position: 'right' }} options={{ cutoutPercentage: 80 }} />
          </div>

          <div className="charts__block charts__bar">
            <HorizontalBar
              data={detailsBar}
              legend={{ display: false }}
              options={{
                responsive: true,
                scales: {
                  yAxes: [
                    {
                      ticks: {
                        fontColor: "grey",
                        fontSize: 18,
                        fontStyle: "normal",
                        beginAtZero: true,
                        min: 0,
                        padding: 5
                      }
                    }
                  ],
                  xAxes: [
                    {
                      gridLines: {
                        display: false
                      },
                      ticks: {
                        fontColor: "grey",
                        fontSize: 18,
                        fontStyle: "normal",
                        beginAtZero: true,
                        min: 0,
                        padding: 5
                      }
                    }
                  ]
                },
  
              }}
            />
          </div>
      </div>
    </div>
  );
};

export default HomePage;
